.winbox {
    background: rgba(255, 255, 255, 0.8);
    border-radius: 4px;
}

.winbox .wb-title {
    color: #000;
}

.winbox .wb-control {
    filter: invert(1);
}

p {
    margin: 0;
    padding: 0;
}

.desktop-entry {
    padding: 4px 8px;
}

.desktop-entry:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

.desktop-entry:active {
    background-color: rgba(255, 255, 255, 0.6);
}

@media (min-width: 576px) {
    .col-no-breakpoint {
        flex: 0 0 150px; /* Set your desired fixed width */
        max-width: 150px;
    }
}
@media (min-width: 768px) {
    .col-no-breakpoint {
        flex: 0 0 150px;
        max-width: 150px;
    }
}
@media (min-width: 992px) {
    .col-no-breakpoint {
        flex: 0 0 150px;
        max-width: 150px;
    }
}
@media (min-width: 1200px) {
    .col-no-breakpoint {
        flex: 0 0 150px;
        max-width: 150px;
    }
}